import * as yup from 'yup';

const radius = yup
  .number()
  .min(0)
  .max(50)
  .meta({
    type: 'range',
    values: [0, 50],
    defaultValue: 0,
    alias: 'r'
  });

const margin = yup
  .number()
  .min(0)
  .max(25)
  .meta({
    type: 'range',
    values: [0, 25],
    defaultValue: 0,
    alias: 'm'
  });

const background = yup
  .string()
  .matches(/^#([0-9a-f]{3}|[0-9a-f]{6}|[0-9a-f]{8})$/i)
  .meta({
    type: 'color',
    defaultValue: '#ffffff',
    alias: 'b'
  });

const width = yup
  .number()
  .min(1)
  .meta({
    type: 'number',
    values: [0, 2048],
    defaultValue: 0,
    alias: 'w'
  });

const height = yup
  .number()
  .min(1)
  .meta({
    type: 'number',
    values: [0, 2048],
    defaultValue: 0,
    alias: 'h'
  });

export default {
  radius: radius,
  r: radius,
  margin: margin,
  m: margin,
  background: background,
  b: background,
  width: width,
  w: width,
  height: height,
  h: height
};
