import * as yup from 'yup';
import defaults from './defaults';

let values: Array<any> = [];

export default yup
  .object({
    ...defaults,
    type: yup
      .string()
      .oneOf((values = ['qr']))
      .meta({
        type: 'select',
        values: values,
        defaultValue: 'qr'
      }),
    color: yup
      .string()
      .matches(/^#([0-9a-f]{3}|[0-9a-f]{6}|[0-9a-f]{8})$/i)
      .meta({
        type: 'color',
        defaultValue: '#000000'
      }),
    correctionLevel: yup
      .string()
      .oneOf((values = ['L', 'M', 'Q', 'H']))
      .meta({
        type: 'select',
        values: values,
        defaultValue: 'M'
      })
  })
  .noUnknown();
