import * as React from 'react';
import SyntaxHighlighter from 'react-syntax-highlighter/dist/esm/prism-light';
import js from 'react-syntax-highlighter/dist/esm/languages/prism/javascript';
import prism from 'react-syntax-highlighter/dist/esm/styles/prism/prism';

import SpriteCollection from '../types/spriteCollection';

// @ts-ignore
SyntaxHighlighter.registerLanguage('javascript', js);

type Props = {
  spriteCollections: SpriteCollection[];
};

export default class Introductions extends React.Component<Props> {
  getHttpApiDescription() {
    let spriteCollections = this.props.spriteCollections.map((spriteCollection, index, array) => {
      // Is penultimate entry
      if (index === array.length - 1) {
        return (
          <React.Fragment key={index}>
            <code>{spriteCollection.id}</code>
          </React.Fragment>
        );
      }

      // Is last entry
      if (index === array.length - 2) {
        return (
          <React.Fragment key={index}>
            <code>{spriteCollection.id}</code>
            {' or '}
          </React.Fragment>
        );
      }

      return (
        <React.Fragment key={index}>
          <code>{spriteCollection.id}</code>
          {', '}
        </React.Fragment>
      );
    });

    return (
      <React.Fragment>
        Replace <code>:sprites</code> with {spriteCollections}. The value of <code>:seed</code> can be anything you like
        - but <strong>don't</strong> use any sensitive or personal data here!
      </React.Fragment>
    );
  }

  render() {
    return (
      <div id="introductions" className="min-vh-100 pt-5 pb-6">
        <div className="container">
          <h2 className="display-1 text-center">How to use</h2>
          <p className="mb-6 text-center lead">
            You can start directly with our free HTTP-API or alternatively use the JavaScript library.
          </p>

          <div className="row mb-5">
            <div className="col-12 offset-xl-1 col-xl-10">
              <h4 className="text-muted text-center">HTTP-API</h4>
              <div className="rounded bg-light p-3">
                <p>Our free HTTP-API is the easiest way to use Avatars. Just use the following URL as image source.</p>
                <SyntaxHighlighter className="bg-white border rounded" style={prism}>
                  {'https://avatars.dicebear.com/api/:sprites/:seed.svg'}
                </SyntaxHighlighter>
                <p>{this.getHttpApiDescription()}</p>
                <hr />
                <p>
                  The used sprite collection may offer additional options, which can be set using GET parameters. For
                  example, to create a happy <i>{this.props.spriteCollections[0].id}</i> avatar with the seed{' '}
                  <code>john</code>, the following URL can be used:
                </p>
                <SyntaxHighlighter className="bg-white border rounded" style={prism}>
                  {`https://avatars.dicebear.com/api/${this.props.spriteCollections[0].id}/john.svg?mood[]=happy`}
                </SyntaxHighlighter>
              </div>
            </div>
          </div>

          <div className="row mb-5">
            <div className="col-12 offset-xl-1 col-xl-10">
              <h4 className="text-muted text-center">NPM</h4>
              <div className="rounded bg-light p-3">
                <p className="mb-4">
                  Choose NPM if you want to use a spriteCollection that is not available via the HTTP-API.
                </p>

                <p>Install the Avatars package with the following command.</p>
                <SyntaxHighlighter className="mb-4 bg-white border rounded" style={prism}>
                  {`$ npm install --save @dicebear/avatars`}
                </SyntaxHighlighter>

                <p>
                  You also need to add a sprite collection. In our example, we will use the{' '}
                  {this.props.spriteCollections[0].id} sprite collection.
                </p>
                <SyntaxHighlighter className="mb-4 bg-white border rounded" style={prism}>
                  {`$ npm install --save ${this.props.spriteCollections[0].name}`}
                </SyntaxHighlighter>

                <p>Now you are ready to create your first Avatar.</p>
                <SyntaxHighlighter
                  className="mb-3 bg-white border rounded"
                  language="javascript"
                  style={prism}
                >{`import Avatars from '@dicebear/avatars';
import sprites from '${this.props.spriteCollections[0].name}';

let options = {};
let avatars = new Avatars(sprites, options);
let svg = avatars.create('custom-seed');`}</SyntaxHighlighter>
              </div>
            </div>
          </div>

          <div className="row">
            <div className="col-12 offset-xl-1 col-xl-10">
              <h4 className="text-muted text-center">Available options</h4>
              <div className="rounded bg-light p-3">
                <p>The following options are available for each sprite collection.</p>
                <table className="table table-bordered table-light table-striped">
                  <thead>
                    <tr className="table-dark">
                      <th>name</th>
                      <th>alias</th>
                      <th>type</th>
                      <th>default</th>
                      <th>description</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>radius</td>
                      <td>r</td>
                      <td>number</td>
                      <td>
                        <code>0</code>
                      </td>
                      <td>Avatar border radius</td>
                    </tr>
                    <tr>
                      <td>base64</td>
                      <td></td>
                      <td>bool</td>
                      <td>
                        <code>false</code>
                      </td>
                      <td>
                        Return avatar as base64 data uri instead of XML <br />{' '}
                        <strong>Not supported by the HTTP API</strong>
                      </td>
                    </tr>
                    <tr>
                      <td>width</td>
                      <td>w</td>
                      <td>number</td>
                      <td>
                        <code>null</code>
                      </td>
                      <td>Fixed width</td>
                    </tr>
                    <tr>
                      <td>height</td>
                      <td>h</td>
                      <td>number</td>
                      <td>
                        <code>null</code>
                      </td>
                      <td>Fixed height</td>
                    </tr>
                    <tr>
                      <td>margin</td>
                      <td>m</td>
                      <td>number</td>
                      <td>
                        <code>0</code>
                      </td>
                      <td>
                        Avatar margin in percent
                        <br /> <strong>HTTP-API limitation</strong> Max value <code>25</code>
                      </td>
                    </tr>
                    <tr>
                      <td>background</td>
                      <td>b</td>
                      <td>string</td>
                      <td>
                        <code>null</code>
                      </td>
                      <td>
                        Any valid color identifier
                        <br /> <strong>HTTP-API limitation</strong> Only hex <em>(3-digit, 6-digit and 8-digit)</em>{' '}
                        values are allowed. Use url encoded hash: <code>%23</code>.
                      </td>
                    </tr>
                    <tr>
                      <td>userAgent</td>
                      <td></td>
                      <td>string</td>
                      <td>
                        <code>window.navigator.userAgent</code>
                      </td>
                      <td>
                        User-Agent for legacy browser fallback
                        <br />
                        <strong>Automatically detected by the HTTP API</strong>
                      </td>
                    </tr>
                  </tbody>
                </table>
                <p>More available options can be found in the README.md of each sprite collection.</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}
