import * as yup from 'yup';
import defaults from './defaults';

export default yup
  .object({
    ...defaults,
    colorful: yup
      .number()
      .oneOf([0, 1])
      .meta({
        type: 'switch',
        values: [0, 1],
        defaultValue: 0
      }),
    deterministic: yup
      .number()
      .oneOf([0, 1])
      .meta({
        type: 'switch',
        values: [0, 1],
        defaultValue: 0
      })
  })
  .noUnknown();
